<template>
   
  <div class="shiping">
    <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
      <li v-for="(item,index) in list" :key="index">
       <video :src="item.video" controls="controls">
         <p>{{item.text}}</p>
       </video>
      </li>
    </ul>

  </div>

</template>
<script>
export default {
  data () {
    return {
      list: [],
      page: 1
    }
  },
  mounted () {
    this.getspData()
  },
  methods: {
    getspData () {
      var url = 'https://www.apiopen.top/satinGodApi?type=5&page=' + this.page
      this.$http.get(url).then(res => {
        this.list = res.data.data
        console.log(this.list.data)
      })
    },
    loadMore () {
      this.page += 1
      this.getspData()
    }
  }
}
</script>
<style>
    .shiping {
        position: fixed;
        /*border: 1px solid red;*/
        /*width: 100%;*/
        overflow: auto;
        top: 50px;
        bottom: 50px;
        left: 10px;
        right: 20px;
    }
    video {
      width: 100%;
      height: 200px;
      margin: 20px 0px;
      border: 2px solid black;
      border-radius: 10px;
    }
</style>



