<template>

  <div class="xiaoshuo">
    <div class="bar">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">汽车</mt-tab-item>
        <mt-tab-item id="2">电影</mt-tab-item>
        <mt-tab-item id="3">娱乐</mt-tab-item>
      </mt-navbar>
      <mt-navbar v-model="selected">
        <mt-tab-item id="4">财经</mt-tab-item>
        <mt-tab-item id="5">体育</mt-tab-item>
        <mt-tab-item id="6">科技</mt-tab-item>
      </mt-navbar>
    </div>
    <div class="container">
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <div class="che" v-for="(item,index) in auto" :key='index'>
             <!--<img :src="{item.picInfo[0].url ==null ? item.picInfo[0].url: '' }" alt="">-->
             <p>车型：{{item.digest}}</p>
             <p>价格：{{item.title}}</p>
             <p>时间：{{item.ptime}}</p>
             <mt-button  type="primary" size="small"><a :href="item.link">详细信息</a></mt-button>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2">
           <div class="che" v-for="(item,index) in dy" :key='index'>
             <!--<img :src="item.picInfo[0].url" alt="">-->
             <p>主题：{{item.digest}}</p>
             <p>电影拷问：{{item.title}}</p>
             <p>时间：{{item.ptime}}</p>
             <mt-button  type="primary" size="small"><a :href="item.link">详细信息</a></mt-button>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="3">
          <div class="che" v-for="(item,index) in ent" :key='index'>
            <!--<img :src="item.picInfo[0].url" alt="">-->
             <p>新闻：{{item.digest}}</p>
             <p>明星相关：{{item.title}}</p>
             <p>时间：{{item.ptime}}</p>
             <mt-button  type="primary" size="small"><a :href="item.link">详细信息</a></mt-button>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="4">
           <div class="che" v-for="(item,index) in money" :key='index'>
             <img :src="item.picInfo[0].url" alt="">
             <p>主题：{{item.digest}}</p>
             <p>电影拷问：{{item.title}}</p>
             <p>时间：{{item.ptime}}</p>
             <mt-button  type="primary" size="small"><a :href="item.link">详细信息</a></mt-button>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="5">
           <div class="che" v-for="(item,index) in sports" :key='index'>
             <img :src="item.picInfo[0].url" alt="">
             <p>体育资讯：{{item.digest}}</p>
             <p>突出点：{{item.title}}</p>
             <p>时间：{{item.ptime}}</p>
             <mt-button  type="primary" size="small"><a :href="item.link">详细信息</a></mt-button>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="6">
            <div class="che" v-for="(item,index) in tech" :key='index'>
             <img :src="item.picInfo[0].url" alt="">
             <p>科技资讯：{{item.digest}}</p>
             <p>新闻拷问：{{item.title}}</p>
             <p>时间：{{item.ptime}}</p>
             <mt-button  type="primary" size="small"><a :href="item.link">详细信息</a></mt-button>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      selected: '1',
      auto: [],
      dy: [],
      ent: [],
      money: [],
      sports: [],
      tech: [],
      aaa: ''
    }
  },
  mounted () {
    this.getdata()
  },
  methods: {
    getdata () {
      var url = 'https://www.apiopen.top/journalismApi'
      this.$http.get(url).then(res => {
        console.log(res.data.data)
        this.auto = res.data.data.auto
        this.dy = res.data.data.dy
        this.ent = res.data.data.ent
        this.money = res.data.data.money
        this.sports = res.data.data.sports
        this.tech = res.data.data.tech
      })
    }
  }
}
</script>

<style>
.xiaoshuo {
  position: fixed;
  /*border: 1px solid red;*/
  /*width: 100%;*/
   overflow: auto;
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
}
.container {

}
.mint-tab-container {
}
.che img {
  width: 100%;
  height: 20%;
}
</style>




