<template>
  
  <div class="tianqi">
    <!--<mt-search   v-model="cityname" cancel-text="取消" onchange='onchange()' placeholder="搜索">
    </mt-search>-->
    <div>天气预报</div>
    <input type="text" v-model="cityname" @blur="onchange()" placeholder="搜索">
    <div id="myChart"></div>
  </div>

</template>
<script>
export default {
  data () {
    return {
      cityname: '深圳',
      warter: [],
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted () {
  },
  created () {
    this.getdata()
  },
  methods: {
    getdata () {
      let url = 'https://www.apiopen.top/weatherApi?city=' + this.cityname
      this.$http.get(url).then(res => {
        console.log(res)
        this.warter = res.body.data
        this.drawLine(this.warter)
      })
    },
    drawLine (data) {
      let myChart = this.$echarts.init(document.getElementById('myChart'))
      var arr = []
      var serArr = []
      for (var i = 0; i < data.forecast.length; i++) {
        arr.push(data.forecast[i].date)
        serArr.push((data.forecast[i].low).substr(2, 3))
      }
      myChart.setOption({
        title: { text: '在Vue中使用echarts' },
        tooltip: {},
        xAxis: {
          data: arr
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'line',
          data: serArr
        }]
      })
    },
    onchange () {
      this.getdata()
    }
  }
}
</script>
<style>
 .tianqi {
    position: fixed;
  /*border: 1px solid red;*/
  /*width: 100%;*/
   overflow: auto;
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
 }
 #myChart{
   position: absolute;
    top:50px;
    height: 400px;
    width:100%;
 }
</style>


