<template>
  <div class="music">
    <iframe class="mm" src="https://music.163.com" frameborder="0"></iframe>
    </audio>
  </div>
    
</template>
<script>
export default {
  data () {
    return {
      musiclist: []
    }
  },
  mounted () {
    this.musicData()
  },
  methods: {
