      var url = 'http://tingapi.ting.baidu.com/v1/restserver/ting?from=android&version=2.4.0&method=baidu.ting.song.getInfos&format=json&songid=354387&ts=1354960702678&e=pOwOqqTY0fS5jmtSdOJBh4XW4rQHDI7EhrJgILD3Z%2FQ%3D&nw=1&bduss='
      this.$http.jsonp(url, { emulateJSON: true }).then(res => {
      })
    }
  }
}
</script>
<style scoped>
.music {
  position: fixed;
  /*border: 1px solid red;*/
  /*width: 100%;*/
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
}
.mm {
  width: 100%;
  height: 100%;
}
.u-footer {
  display: none;
}
</style>



