<template>
  

  <div class="home">
    <!--轮播图-->
    <div class="swipeConnet">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="(item,index) in imgUrls" :key='index'>
          <img :src="item.url" alt="">
        </mt-swipe-item>
      </mt-swipe>
    </div>

    <!--九宫格-->

    <div class="middle clearfi">

      <div class="demo-avatar cmd">
        <router-link to="/music">
          <img class="img" src="./../../assets/goto/音乐.png" alt="">
          <p>音乐</p>
        </router-link>
      </div>
      <div class="demo-avatar cmd clearfi">
        <router-link to="/xiaoshuo">
          <img class="img" src="./../../assets/goto/小说.png" alt="">
          <p>新闻</p>
        </router-link>
      </div>
      <div class="demo-avatar cmd clearfi">
        <router-link to="/tianqi">
          <img class="img" src="./../../assets/goto/天气.png" alt="">
          <p>天气</p>
        </router-link>
      </div>
      <div class="demo-avatar cmd sp clearfi">
        <router-link to="/weather">
          <img class="img" src="./../../assets/goto/图片.png" alt="">
          <p>美图</p>
        </router-link>
      </div>
       <div class="demo-avatar cmd  clearfi">
        <router-link to="/shiping">
          <img class="img sp" src="./../../assets/goto/视频.png" alt="">
          <p class='ps'>视频</p>
        </router-link>
      </div>
      <div class="demo-avatar cmd clearfi">
        <router-link to="/weather">
          <img class="img" src="./../../assets/goto/机器人.png" alt="">
          <p>机器人</p>
        </router-link>
      </div>

    </div>

  </div>

</template>

<script type="text/ecmascript-6">
import music from './music'
export default {
  data () {
    return {
      myname: '首页',
      imgUrls: []
    }
  },
  comments: {
    music
  },
  mounted () {
    this.swipdata()
  },
  methods: {
    swipdata () {
      var url = 'https://www.apiopen.top/meituApi?page=1'
      this.$http.get(url).then(res => {
        this.imgUrls = res.body.data.slice(4, 9)
      })
    }
  }
}
</script>

<style scoped>

.home {
  position: fixed;
  /*border: 1px solid red;*/
  /*width: 100%;*/
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
}

.swipeConnet {
  height: 40%;
}
.middle {
  padding: 0;
  margin: 0;
  width: 100%;
  margin: auto
}
.swipeConnet img {
  width: 100%;
  height: 100%;
}
.cmd {
  float: left;
  width: 33%;
  height: 110px;
  font-size: 0;
  margin-left: 1px;
  margin-top: 1px;
   background-color: #e3e3e5;
}

.cmd .img {
  display: block;
  padding-top: 10%;
  padding-bottom: 10px;
  margin: auto;
   width: 50%;
   height: 50%;
}
.cmd p {
  margin-bottom: 10px;
  color:black;
  text-align: center;
  height: 20px;
  font-size: 16px;
}
.cmd a {
  text-decoration: none;
}
.clearfix:after{
  content: "020"; 
  display: block; 
  height: 0; 
  clear: both; 
  visibility: hidden;  
  }
 

</style>



